import React, { createElement, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import SignatureCanvas from 'react-signature-canvas';
import '../../../comp.css';
/**
 * DashSign is a canvas for capturing
 * hand written signatures
 */
const DashSign = (props) => {
    
    const {id,penColor,backgroundColor, n_submit,style,data,setProps} = props;
    const colorOverride={'backgroundColor':backgroundColor}
    //const colorClear={'color':clearColor}
    //const colorHover={'color':hoverColor}
    const sigCanvas=useRef();
    const [imageURL,setImageURL]=useState(null);
    const saveURL=()=>{
        const URL=sigCanvas.current.getTrimmedCanvas().toDataURL("image/png");
        setImageURL(URL)
        sigCanvas.current.clear()
        setProps({n_submit:n_submit+1,data:URL})
    };
    
    const download=(imageURL)=>{
        const dlink=document.createElement("a");
        dlink.setAttribute("href",imageURL)
        dlink.setAttribute("download","signature.png")
        dlink.click();
    };
    return (
        <div id={id} className='app'>
            <div style={colorOverride} className='sigPadContainer'>
                <SignatureCanvas 
                    penColor={penColor}
                    canvasProps={{className: 'sigCanvas'}}
                    ref={sigCanvas}
                    
                    //ref={(ref) => { this.sigCanvas = ref }}
                />
                <hr/>
                <div className='sigButContainer'>
                    <button onClick={()=>sigCanvas.current.clear()}>
                        Clear
                    </button>
                </div>
                
                </div>
            <div className='sigBottom'>
                <button onClick={saveURL}>
                    Save
                </button>
            </div>
        </div>
        
    );
}

DashSign.defaultProps = {backgroundColor:'gainsboro',
                        penColor:'black',
                        n_submit:0};

DashSign.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,
    /**
     * The pen color of the signature component
     */
    penColor:PropTypes.string,
    /**
     * The background color of the signature canvas
     */
    backgroundColor:PropTypes.string,
    /**
     * The number of times the save button has been clicked
     */
    n_submit:PropTypes.number,
        /**
     * The style props of signature div
     */
    style: PropTypes.object,
    /**
     * Base 64 data string for the resulting image
     */
    data:PropTypes.string,
    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};

export default DashSign;
